import React, { Component } from 'react';
import styled from 'styled-components';
import {
  MastHead,
  AboutMe,
  Work,
  Contact,
  Arrow,
  Menu,
  MenuButton
} from './components';

const CenterDiv = styled.div`
  text-align: center;
`;

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      menuOpen: false
    };
  }

  toggleMenu = () => {
    const { menuOpen } = this.state;
    this.setState({ menuOpen: !menuOpen });
  };

  render() {
    const { menuOpen } = this.state;

    return (
      <div>
        <MenuButton open={menuOpen} onToggle={this.toggleMenu}/>
        <Menu open={menuOpen}/>
        <MastHead/>
        <AboutMe/>
        <Work/>
        <CenterDiv>
          <Arrow/>
        </CenterDiv>
        <Contact/>
        <p className="copyrightText"> Copyright 2017. All rights reserved.</p>
      </div>
    );
  }
}

export default App;



